const { __ } = wp.i18n;
const { InspectorControls } = wp.blockEditor;
const { PanelBody, RangeControl } = wp.components;
const { createHigherOrderComponent } = wp.compose;

const withMarginPaddingControl = createHigherOrderComponent((BlockEdit) => {
  return (props) => {
    const { attributes, setAttributes } = props;

    return (
      <>
        <BlockEdit {...props} />
        <InspectorControls>
          <PanelBody title={__("Margin & Padding Settings", "text-domain")}>
            <RangeControl
              label={__("Margin", "text-domain")}
              value={attributes.margin || 0}
              onChange={(value) => setAttributes({ margin: value })}
              min={0}
              max={100}
            />
            <RangeControl
              label={__("Padding", "text-domain")}
              value={attributes.padding || 0}
              onChange={(value) => setAttributes({ padding: value })}
              min={0}
              max={100}
            />
          </PanelBody>
        </InspectorControls>
      </>
    );
  };
}, "withMarginPaddingControl");

wp.hooks.addFilter(
  "editor.BlockEdit",
  "my-plugin/with-margin-padding-control",
  withMarginPaddingControl
);
